import React from 'react';

type SwitchProps = {
  readonly onChange: () => void;
  readonly checked: boolean;
  readonly label: string;
  readonly disabled?: boolean;
};

export const Switch: React.FC<SwitchProps> = ({ label, onChange, checked, disabled, ...props }) => {
  return (
      <div className="relative" aria-label={label}>
        <input className="sr-only" {...props} />
        <button
          onClick={onChange}
          tabIndex={0}
          className={`block ${
            !disabled && checked ? 'bg-accent' : 'bg-form'
          } w-14 h-6 rounded-lg focus:outline-none`}
          disabled={disabled}
          {...props}
        ></button>
        <div
          className={`dot absolute inset-y-0.5 ${
            !disabled && checked ? 'left-3 translate-x-full' : 'left-1'
          }   bg-body1 w-5 h-5 rounded-full transition transform pointer-events-none`}
        ></div>
      </div>
  );
};
